<script>
import useLogout from '../composables/useLogout'
import { useRouter} from 'vue-router'
import getUser from '@/composables/getUser'
import { ref, watch, watchEffect, onMounted } from 'vue'
import { userStore } from '@/store/userStore'
import { coursesStore } from '@/store/coursesStore'
import ModalMenu from './ModalMenu.vue'

export default {
    components: { ModalMenu },
    setup(props, context) {
      const store = userStore()
      const cstore = coursesStore()
        const {logout, error } = useLogout()
        const router = useRouter()
        const isAdmin = ref()
        const displayName = ref()
        const ModuleShow = ref(false)
        const matty = ref(localStorage.getItem('displayName'))
        

        store.$subscribe((login, state)=>{
          displayName.value = store.displayName
          isAdmin.value = store.admin
          console.log('displayname: ', displayName.value)
          console.log('how:', matty.value)
        })

        onMounted(() =>{
          displayName.value = store.displayName
          isAdmin.value = store.admin
          console.log(`the component is now mounted.`)

        })

        const showModule = () =>{
          ModuleShow.value=true;
        }

        const modalClose = () =>{
          ModuleShow.value=false
        }

        const handleClick =async () => {
            store.$reset()
            cstore.$reset()
            await logout()
            router.push({ name: 'Login'})
        }

    return { handleClick, isAdmin, displayName, showModule, ModuleShow, modalClose }
    }
}
</script>

